.tooltip {
  position: relative;
  display: inline-block;
  z-index:999;
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  background-color: #4A80FF;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 5px;

  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
  left: 115%;
  -webkit-transform: translateY(40%);
          transform: translateY(40%);
}


.tooltip:hover .tooltiptext {
  visibility: visible;
}

.tooltip .tooltiptext::after {
  content: " ";
  position: absolute;
  top: 50%;
  right: 100%; /* To the left of the tooltip */
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent #4A80FF transparent transparent;
}



/* Tooltip text */
.tooltip .dataset_tooltiptext {
  width:100%;
  visibility: hidden;
  background-color: #4A80FF;
  color: #fff;
  text-align: center;
  padding: 10px;
  border-radius: 5px;

  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 999;
  left: -5%;
  top:-3%;
}

.tooltip:hover .dataset_tooltiptext {
  visibility: visible;
}

.tooltip .dataset_tooltiptext::after {
  content: "";

  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #4A80FF transparent transparent transparent;
}




/* Tooltip text */
.tooltip .select_tooltiptext {
  width:100%;
  visibility: hidden;
  background-color: white;
  color: #303030;
  text-align: center;
  padding: 10px;
  border-radius: 5px;

  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 999;
  left: 50%;
  top:-10%;
  border: 3px solid #303030;
}

.tooltip:hover .select_tooltiptext {
  visibility: visible;
}











/* kebab 아이콘 영역 */
.kebab {
  cursor: pointer;
  position: absolute;
  display: inline-block;
  box-sizing: border-box;
  padding: 0px 0;
  top: 10px;
  right: 10px;
  display: flex;
  flex-direction: column;
  width: 25px;
  align-items: center;
  z-index:10;
}

/* kebab 아이콘 - 점 */
.kebab figure {
  width: 4px;
  height: 4px;
  border-radius: 5px;
  background: white;
  margin: 2px 0;
}

/* kebab hover */
.kebab:hover {
  background-color: #303030;
  border-radius: 50%;
}

/* kebab 활성화 시 리스트 */
.kebab .dropdown {
  position: absolute;
  right: 0;
  top: 15px;
  right: 25px;
  height: 40px;
  width: 135px;
  transition: all 0.25s ease-out;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: 100% 0;
          transform-origin: 100% 0;
  box-shadow: 0 2px 4px 0 rgb(0 0 0 / 16%), 0 2px 8px 0 rgb(0 0 0 / 12%);
  color: #bdbfc2;
}

ol, ul {
  list-style: none;
}

/* kebab 활성화 시 리스트 */
.kebab .dropdown.active {
  position: absolute;
  top: 30px;
  /* height: 90px; */
  height: auto;
  width: 135px;
  right: 10px;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: all 0.25s cubic-bezier(0.5, 1.8, 0.9, 0.8);
  background-color: #909090;
  display: flex;
  justify-content: flex-start;
  padding-left: 0px;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.15);
  flex-direction: column;
}

/* kebab 버튼 리스트 - 삭제 */
.kebab-delete {
  color:white;
  width: 100%;
  display: flex;
  justify-content: flex-start;
